/* Dark Theme - Colours
1. Background (0dp elevation surface overlay)
2. Surface (with 1dp elevation surface overlay)
3. Primary
4. Secondary
5. On background
6. On Surface
7. On Primary
8. On Secondary

1.#121212
2.#121212
3.#BB86FC
4.#03DAC6
5.#FFFFFF
6.#FFFFFF
7.#000000
8.#000000



| elevation | overlay |   hex   |
| --------- | ------- | ------- |
|   00dp    |    0%   | #121212 |
|   01dp    |    5%   | #1e1e1e |
|   02dp    |    7%   | #222222 |
|   03dp    |    8%   | #242424 |
|   04dp    |    9%   | #272727 |
|   06dp    |   11%   | #2c2c2c |
|   08dp    |   12%   | #2e2e2e |
|   12dp    |   14%   | #333333 |
|   16dp    |   15%   | #343434 |
|   24dp    |   16%   | #383838 |


Component elevation values
1. Nav drawer: 16dp
2. App bar: 4dp
3. Card: 1dp to 8dp
4. FAB: 6dp
5. Button: 2dp to 8dp
6. Dialog: 24dp
*/

body {
  background-color: #ECECEC;
}
.App {
  font-family: 'Sawarabi Gothic';
  background-color: #ECECEC ;
}

section {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100vw;
  background-color: #ECECEC ;
  color: black;
  }

/* HOME SECTION */

#home {
  height: 100vh;
  width: 100vw;
  background-color: #430391;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

#contact {
  height: 100vh;
  /* background-color: #430391;
  color: white; */
}

#projects {
  padding-top: 5vh;
  height: 100vh;
  background-color: white;
}




#HomeDiv {
  position: absolute;
  padding-left: 0%;
  top: 25vh;
}

#HomeP {
  margin-bottom: 5px;
  font-size: 2vw;
  color: #64ffda;
}

.HomeH {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 7vw;
  overflow: hidden; /* Ensures the text is not revealed until the animation */
  border-right: .1em solid white; /* The typewriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  display: inline-block; /* Ensures the width is calculated correctly */
  letter-spacing: 4px;
  animation:
    typing 4s steps(40, end),
    blink-caret .75s step-end infinite;
}

/* The typing effect */
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: white }
}


#HomeH2 {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 3vw;
}


/* ABOUT SECTION */

#AboutRow {
  display: flex;
  justify-content: center;
  width: 90%;
  height: 80%;
}

#AboutColumn {
  flex: 50%;
  margin-right: 10vh;
  /* margin-right: 50px; */
}

#AboutColumn:last-child {
  margin-right: -1vw;
}

 

#AboutH1 {
  font-size: 2.5vw;
  color: #430391;
  margin-bottom: -10px;
}

#AboutP {
  font-size: 1.5vw;
} 

#AboutSkill {
  font-size: 2vw;
  color: #430391;
  margin-bottom: -20px;

}

#AboutUL {
  /* display: flex;
  align-items: center; */
  list-style: none;
  margin-bottom: -5px;
  margin-top: 25px;
  padding-inline-start: 40px;
  width: 100%;
}

#AboutUL > li {
  display: inline-block;
  position: relative;
  margin-right: 3vw;
  color: black;
  font-size: 1.2vw;
}

#AboutUL > li:before {
  content: ""; /* Add an empty content */
  display: inline-block; /* Display the bullet point inline with the text */
  position: absolute; /* Set the position property to absolute */
  left: -2vw; /* Position the bullet point to the left of the list item */
  top: 50%; /* Position the bullet point vertically in the middle of the text */
  transform: translateY(-50%); /* Center the bullet point vertically */
  width: .5em; /* Set the width of the bullet point */
  height: .5em; /* Set the height of the bullet point */
  border-radius: 50%; /* Make the bullet point circular */
  background-color: #430391; /* Set the background color of the bullet point */
}

#AboutUL > li:first-child:before {
  display: none;
}

/* NAVIGATION SECTION */

.nav {
  display: flex;
  align-items: center;
  /* position: -webkit-sticky; */
  position: fixed;
  z-index: 2;
  height: 6vw;
  width: 100%;  
  /* background-color:#2b0465; */
  background-color: #430391;
  box-shadow: 0 0.125rem 0.25rem 0 rgb(0 0 0 / 11%);
  color: white;
  /* box-shadow: none; */

}

.nav__container {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.nav__container__actions {
  display: inline-flex;
  flex-flow: row nowrap;
  padding-left: 5vh;
  flex: 1;
}

.active {
  font-weight: bold;
  color: #64ffda;
}

.nav_ul {
  display: flex;
  gap: 3vh;
  list-style: none;
  padding: 0;
}

.nav_li {
  cursor: pointer;
  font-size: 2vw;
}

.nav_li:hover {
  color: #64ffda;
}

.icons {
  cursor: pointer;
  align-items: right;
  list-style: none;
  padding: 1vh;
  color: white;
  font-size: 3vw;
}

.iconsLast {
  cursor: pointer;
  align-items: right;
  list-style: none;
  padding: 1vh;
  color: white;
  font-size: 3vw;
  padding-right: 2vw;
}




a:hover {
  color: #64ffda;
}

a {
  color: white;;
  text-decoration: none;
}

.HomeHeader {
  color:white;
}

/* PROJECT  */

.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 3%;
  justify-content: center;
  align-items: center;
  padding-left: 20vh;
  padding-right: 20vh;
  margin-top: -10vh;
}

.grid-item {
  background-color: white;
  box-shadow: 0 0 10px rgba(43, 4, 101, 0.8);
  border-radius: 5px;
  padding: 1rem;
  text-align: center;
  min-height: 30vh;
  min-width: 30vh;
  font-size: 2vh;
  
}

.iconsP {
  cursor: pointer;
  align-items: right;
  list-style: none;
  color: black;
  transition: color 0.2s ease-in-out;
  font-size: 7vh;
}

.iconsP:hover {
  /* color: #2b0465; */
  color: #430391;
}


.grid-item h3 {
  margin-top: 0;
  margin-bottom: 0.5em;
}

.grid-item p {
  margin-top: 0;
  margin-bottom: 0.5em;
}

/* WebKit-based browsers */
::-webkit-scrollbar {
  width: 1vw; /* Use viewport width */
}

::-webkit-scrollbar-track {
  background-color: #b99ed7;
}

::-webkit-scrollbar-thumb {
  background-color: #430391;
  border-radius: 10px;
}



/* CONTACT SECTION */

.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  padding: 2vh;
  box-shadow: 0 0 10px rgba(43, 4, 101, 0.8);
  border-radius: 5%;
}

.photo-container {
  width: 50vh;
  height: 50vh;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(43, 4, 101, 0.8);

}

.photo-container img {
  width: 100%;
  height: auto;
  
}

.title {
  margin-top: 0;
  text-align: center;
  font-size: 7vh;
  margin-bottom: 0;
  color: #430391;

}

.subtitle {
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  color: #430391;
  font-size: 3vh;
}

.contactB {
  cursor: pointer;
  color: #430391;
  font-size: 6vh;
}


/* Footer Section */


#footer {
  height: 20vh;
  width: 100vw;
  background-color: #430391;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}